import { Link } from 'gatsby'
import get from 'lodash/get'
import React from 'react'
import map from 'lodash/map'
import Img from 'gatsby-image'
import styled from 'styled-components'
import ScrollToTop from 'react-scroll-up'

import { screen } from '@style/screen'
import Adsense from 'components/Adsense'
import { Container, Section} from '@style/global'
import {globalType , H2} from '@style/typography'
import { color } from '@style/color'
import { IconToTop } from 'components/Icon/IconToTop'

const getAd = (isIndex, adsense) => {
  return !isIndex ? <Adsense clientId={adsense} slotId="" format="auto" /> : ''
}

const getDescription = body => {
  body = body.replace(/<blockquote>/g, '<blockquote class="blockquote">')
  if (body.match('<!--more-->')) {
    body = body.split('<!--more-->')
    if (typeof body[0] !== 'undefined') {
      return body[0]
    }
  }
  return body
}

const Button = ({ path, label, primary }) => (
  <Link className="readmore" to={path}>
    <span
      className={`btn btn-outline-primary btn-block ${
        primary ? 'btn-outline-primary' : 'btn-outline-secondary'
      }`}
    >
      {label}
    </span>
  </Link>
)

const Role = ({ items }) =>
  map(items, (item, i) => {
    return <LsRole key={i}>{item}</LsRole>
  })

const Post = ({ data, options }) => {
  const {
    role,
    description,
    title,
    path,
    date,
    image,
  } = data.frontmatter
  const { isIndex, adsense } = options
  const html = get(data, 'html')
  const isMore = isIndex && !!html.match('<!--more-->')
  const fluid = get(image, 'childImageSharp.fluid')

  return (
    <Container key={path}>
      <Mas>
        <MasInfo>
          <TitleInfo><Link to="/">Work</Link></TitleInfo>
          <H2>{title}</H2>
          <p>{description}</p>
        </MasInfo>
        <MasInfo>
          <TitleInfo>Year</TitleInfo>
            <Info><time dateTime={date}>{date}</time></Info>
          <TitleInfo>Responsibilities</TitleInfo>
            <Info>{Role({ items: role })}</Info>
        </MasInfo>
        <MasImg>
          {fluid ? (
            <Img
              fluid={fluid}
              style={{ display: 'block', margin: '0 auto' }}
            />
          ) : (
            ''
          )}
        </MasImg>
      </Mas>
      <Content
        dangerouslySetInnerHTML={{
          __html: isMore ? getDescription(html) : html,
        }}
      />
      {isMore ? Button({ path, label: 'MORE', primary: true }) : ''}
      {getAd(isIndex, adsense)}
      <ScrollToTop showUnder={360}>
        <IconToTop/>
      </ScrollToTop>
      <Section>
        <Back>
          <Link className="text-center" to="/">
            <h4>Back to Index</h4>
          </Link>
        </Back>
      </Section>
    </Container>
  )
}

export default Post

const Mas = styled.div`
  padding-top: 20vh;
  padding-bottom: 15vh;
  display: grid;
  grid-template-columns: 3fr 2fr 2fr;
  grid-gap: 70px;
  border-bottom: solid 1px ${color.grayscale.S500};
  @media (max-width: ${screen.lg}) {
    grid-template-columns: unset;
    grid-gap: 20px;
    padding-bottom: 5vh;
    margin-top: 0;
    padding-top: 100px;
    border-bottom: none;
  }  
  @media (max-width: ${screen.md}) {   
    border-bottom: solid 1px ${color.grayscale.S500};
    padding-top: 40px;
  }  
`

const MasInfo = styled.div`
  ${H2} {
    font-weight: bold;
  }
`

const MasImg = styled.div`
  @media (max-width: ${screen.md}) {
    order: -1;  
  }
  @media (mix-width: ${screen.md}), (max-width: ${screen.lg}) {
    margin-top: 20px;
  }
`
const TitleInfo = styled.div`
  ${globalType.size.primary};
  ${globalType.size.xs};
  font-weight: bold;
  margin: 20px 0 10px 0;
`

const Info = styled.div`
  ${globalType.size.sm};
`

const LsRole = styled.div`
  ::before {
    content: ' ';
  }
`

const Back = styled.div`
  border-top: 1px solid ${color.grayscale.S400};
  padding-top: 100px;
`

const Content = styled.div`
  padding-top: 50px;
  width: 720px;
  @media (max-width: ${screen.lg}) {
    width: 90%;
  }
  @media (max-width: ${screen.md}) {
    width: 100%;
  }
  h1, h2, h3, h4, h5, h6 {
    padding-top: 10px;
    margin: 20px 0 20px 0;
  }

  p, ul, ol {
    margin: 20px 0 20px 0;
  }

  a{
    transition: color .4s ease-out;
    position: relative;
    :hover {
      color: ${color.secondary.S900};
      right: 0;
      text-decoration: none;
    }
    
    :before {
      border-bottom: 0.3em solid ${color.support.S300};
      content: "";
      position: absolute;
      z-index: -1;
        right: 100%;
        bottom: .14em;
        left: 0;
        right: 0;
      transition: all .4s cubic-bezier(0,.5,0,1),
                  border-color .4s ease-out;
    }

    :hover:before {
      border-bottom: 1em solid;
      border-color: ${color.support.S400};
    }
    
    :hover:after {
    right: 0;
    }
  }

  blockquote {
    border-left: 5px solid ${color.grayscale.S600};
    padding: 0.45em 1.5em 1.4em 1.5em;

    a {
      display: block;
      word-break: break-word;
    }
  }

  hr{
    padding: 10px 0;
    border-top: none !important;  
    @media (max-width: ${screen.md}) {
      padding: 5px 0;
    }   
  }

  a:hover img {
    animation: pulse 0.5s;
  }

  figcaption::before{
    content:" ";
    ${globalType.size.base};
  }

  figcaption{
    ${globalType.size.xs};
    color: ${color.grayscale.S800};
    margin-bottom: 20px;
    margin-top: -5px;
    @media (max-width: ${screen.lg}) {
      margin-bottom: 5px;
    }
  }
`